import { loadCategoryMenu } from "@storefront/core/data-resolver/categories";
import config from "@config";
import { Logger } from "@storefront/core/lib/logger";

const state = () => ({
  sidebarCategoryItems: null
});

const actions = {
  async loadSidebarCategory({ commit }) {
    const sidebarCategoryId = config.sidebarCategoryId;
    const category = await loadCategoryMenu(sidebarCategoryId).catch((e) => {
      Logger.error("loadMenu", "menu.ts", e)();
      throw e;
    });

    commit("setSidebarCategoryItems", category.children);
  },
};

const mutations = {
  setSidebarCategoryItems(state, payload) {
    state.sidebarCategoryItems = payload;
  },
};

const getters = {
  getSidebarCategoryItems: (state) => state.sidebarCategoryItems,
};

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters,
};
